.menu{
    margin-top: 1%;
    margin-bottom: 1%;
    /* padding: 20px; */
    display: flex;
    flex-wrap: wrap; 
    justify-content: center; 
    /* gap: 25px; */
    flex-basis: 1rem; 
    align-self: normal;
    /* background-color: #7EA3F1; */
}

.card{
    /* width: 18rem; */
    text-align: center;
    font-style: italic;
    border-radius: 20px;
    /* box-shadow: 0 0 15px #688ce1;   */
    border: 1px solid #D0D8F3;  
}
  
.card:hover {
    /* border: 2px solid red;  */
    transition: .2s ease-in-out;
    transform: scale(1.1);
}

.card-body {
    display: flex;
    flex-direction: column;
    /* justify-content: flex-start;  */
    /* height: 100%;  */
    flex-wrap: wrap; 
}

.card-title{
    font-size: 18px;
}

.card-text{
    color: black;
}

.btn1 {
    margin-top: auto;
    border-radius: 50px;
    font-size: 14px;
    background-color: #7678ed;
    border: 3px solid #7678ed;
    padding: 3px 20px;
    color: white;
    margin-left: 60px;
    margin-right: 60px;
}

.btn1:active {
    box-shadow: 0 0 15px #ff006e;   
}

.modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 0.3rem;
    outline: 0;
  }