.floating-buttons {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    z-index: 1000;
}

.fab-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #007bff;
    color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border: none;
    cursor: pointer;
    transition: width 0.3s ease, border-radius 0.3s ease, background-color 0.3s ease;
    position: relative;
    overflow: hidden;
}

.fab-btn:hover {
    width: 160px; 
    border-radius: 30px; 
}

.fab-icon {
    font-size: 24px;
    transition: transform 0.3s ease;
}

.fab-btn:hover .fab-icon {
    transform: translateX(-40px); 
}

.fab-text {
    position: absolute;
    left: 60px;
    opacity: 0;
    white-space: nowrap;
    transition: opacity 0.3s ease, left 0.3s ease;
    font-size: 14px;
    color: white;
}

.fab-btn:hover .fab-text {
    opacity: 1;
    left: 60px; 
}

.nav-pills .nav-link {
    color: blue;
    cursor: pointer;
    font-size: 17px;
    font-style: italic;
    transition: color 0.3s ease, background-color 0.3s ease;
}

.nav-pills .nav-link:hover {
    color: white !important;
    background-color: #7EA3F1;
}

.nav-pills .nav-link.active {
    color: white !important;
    background-color: #007bff;
}



@media (max-width: 768px) {

    .container {
        padding-top: 60px;        
    }
    
    .nav-pills .nav-link {
        font-size: 15px;
        padding: 10px 12px;
    }

}

