.list-group{
  width: 50%;
  margin-left: 20%;
}

.cart-title {
  text-align: center;
  margin-bottom: 20px;
  padding-right: 6%;
  color: rgb(49, 47, 47);
 
}

.items {
  margin-bottom: 0;
  color: rgb(49, 47, 47);
  text-align: start;
}

.quantity{
  padding: 4px 5px;
  border-radius: 5px;
  color: #3973f0;
  margin: 0;
  text-align: center;
  font-size: 18px;
}

.quantity-controls {
  display: flex;
  align-items: center;
}

.quantity-box {
  border: 2px solid blue;
  padding: 5px 10px;
  border-radius: 5px;
  color: blue;
  margin: 0;
  text-align: center;
  line-height: 1;
}

.btn2{
  padding: 7px 10px;
  margin-left: 2%;
}

.btn2:hover{
  box-shadow: 0 0 15px #ff006e;   
}

.modal-dialog1 {
  max-width: 800px;
  margin: 4.5rem auto;
  
}

.modal-content1 {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
  color: black;
}

.modal-header1 {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #dee2e6;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
}

.modal-body1 {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  /* color: rgb(49, 47, 47); */
}

.order-summary, .customer-details {
  width: 45%;
  color: rgb(49, 47, 47);
}

.order-summary li{
  color: rgb(49, 47, 47);
}

.customer-details label{
  color: rgb(49, 47, 47);
}

.modal-footer1 {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 0.75rem;
  border-top: 1px solid #dee2e6;
}

.toast {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1050;
}

.toast-header {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 1.25rem;
  background-color: #007bff;
  color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
}

.toast-body {
  padding: 0.75rem 1.25rem;
  color: rgb(49, 47, 47);
}
