/* Reset some basic styles */
html, body, #root .App {
    height: 100%;
    margin: 0;
    padding: 0;
  }
  
  /* The main container */
  .App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Content should take up all the space available, pushing the footer to the bottom */
  .content {
    flex: 1;
    /* display: flex;
    flex-direction: column; */
  }
  
