.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1050;
}

.modal-dialog {
    background-color: #fff;
    border-radius: 8px;
    max-width: 500px;
    width: 40%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: slideDown 0.3s ease-out;
    display: flex;
    flex-direction: column;
    margin-top: 4%;
 
}

.modal-content {
    position: relative;
    display: flex;
    flex-direction: column; 
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #dee2e6;
    flex-direction: row;
    height: 55px; 
}

.modal-title {
    font-size: 1.5rem;
    color: #333;
}

.close {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #000;
    line-height: 1px;
    margin-left: 10px;
}


.modal-body {
    display: flex;
    flex-direction: column; 
    gap: 1px; 
    color: #000;

}

.modal-body .form-group {
    display: flex;
    flex-direction: column; 
}

.modal-body .form-group label {
    font-size: 1rem;
    color: black;
    margin-bottom: 5px;
}

.modal-body .form-group input,
.modal-body .form-group textarea,
.modal-body .form-group select option {
    width: 100%;
    border-radius: 4px;
    border: 1px solid #ced4da;
    font-size: 1rem;
    color: #333;
    background-color: #f9f9f9;
    
}

.modal-body .form-group textarea {
    resize: vertical;
}

.modal-body .form-group img {
    display: block;
    margin-top: 10px;
    max-width: 100px;
    height: auto;
    border-radius: 4px;
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    border-top: 1px solid #dee2e6;
}

.modal-footer .btn {
    margin-left: 10px;
}

@keyframes slideDown {
    from {
        transform: translateY(-30px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@media (max-width: 768px) {
    .modal-dialog {
        max-width: 90%;
    }
}
