.aboutus-container {
    min-height: 200px;
    text-align: center;
    padding: 0% 5% 0 5%;
    color: var(--text-color);    
}


.aboutus-img1 {
    padding-top: 23%;
    border-radius: 10px;   
    padding-bottom: 5%;     
}


.aboutus-img1 img{
    max-width: 100%;
    width: 85%;
    height: 50vh;  
    align-self: center;
    border-radius: 10px;  
    box-shadow: 0 0 15px red;
    
}


.aboutus {
    padding-top: 20%;
    font-style: italic;
    text-align: center;
    color: var(--text-color);
    
}



.aboutus p{
    color: #6668e7;
    padding-top: 15px;
    text-shadow: 0px 0px 15px white; 
}

.aboutus-img {
    padding-bottom: 5%;
    padding-right: 10%;
    padding-left: 10%;
}


.aboutus-img img {
    max-width: 1000%;
    width: 100%;
    height: 50vh;  
    align-self: center;
    border-radius: 10px;  
}

.aboutus-img img:hover {
    transform: scale(1.1) translateY(3px);
    transition: all .2s ease-in-out;
}

.aboutus-img1 img:hover {
    transform: scale(1.1) translateY(3px);
    transition: all .2s ease-in-out;
    border: 1px solid var(--main-color); 
}
    


.title{
    padding-top: 7%;
    padding-bottom: 3%;
    text-align: center;
    font-style: italic;

}

.title img{  
    height: 50px;
}