.footer {
    background-color:#7EA3F1;
    padding: 5px 0;
    color: whitesmoke;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  
  .contact-container {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
  }
  
  .contact {
    padding: 5px;
  }
  
  .contact img {
    width: 45px;
    height: auto;
    margin-bottom: 10px;
   
  }
  
  .contact h2 {
    font-size: 15px;
    color: #4B4B4B;    
  }
  
  .contact h4 {
    margin: 0px 0px;
    font-size: 13px;
    color: #FFFFFF;
  }
  

  .contact img:hover {
    transform: scale(1.1) translateY(3px);
    transition: all .2s ease-in-out;
    
}
  